<template>
    <div class="about-info-editor">
        <navigate :title="navigateTitle" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-form class="form" :model="form" label-position="left" label-width="100" ref="form">
                <mu-form-item class="form-item-s border-1px-b" prop="alias" label="昵称" :rules="aliasRules">
                    <mu-text-field v-model="form.alias" prop="alias"
                                   :placeholder="mode !== 1 ? '' : '请输入昵称'"
                                   :disabled="mode !== 1" solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="gender" label="性别">
                    <mu-text-field v-model="form.sex" prop="sex"
                                   :placeholder="mode !== 1 ? '' : '请输入性别'"
                                   @click="handlerSelected"
                                   :disabled="mode !== 1" solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="age" label="年龄" :rules="ageRules">
                    <mu-text-field v-model="form.age" prop="age"
                                   :placeholder="mode !== 1 ? '' : '请输入年龄'"
                                   :disabled="mode !== 1" solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="workAge" label="从业年限" :rules="workAgeRules">
                    <mu-text-field v-model="form.workAge" prop="workAge"
                                   :placeholder="mode !== 1 ? '' : '请输入从业年限'"
                                   :disabled="mode !== 1" solo/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="company" label="公司名称" :rules="companyRules">
                    <mu-text-field v-model="form.company" prop="company"
                                   :placeholder="mode !== 1 ? '' : '请输入公司名称'"
                                   :disabled="mode !== 1" solo/>
                </mu-form-item>
                <div class="form-item-buttons" v-if="mode === 1">
                    <mu-button class="button" @click="submit" v-loading="loading"
                               data-mu-loading-size="24"
                               :disabled="loading"
                               flat>提交
                    </mu-button>
                    <mu-button class="button clear" @click="clear" flat>重置</mu-button>
                    <mu-button class="button clear" @click="over" flat>取消</mu-button>
                </div>
            </mu-form>
            <div class="form-button-wrapper" v-if="mode !== 1">
                <mu-button class="button" color="#fff" @click="handlerToggleMode" flat>修改</mu-button>
            </div>
        </div>
        <mu-bottom-sheet :open.sync="open" lock-scroll>
            <mu-list @item-click="closeBottomSheet">
                <mu-list-item @click="handlerToggleGender('男')" button>
                    <mu-list-item-action>
                        <mu-icon value="sentiment_satisfied" color="#2196f3"></mu-icon>
                    </mu-list-item-action>
                    <mu-list-item-title>男</mu-list-item-title>
                </mu-list-item>
                <mu-list-item @click="handlerToggleGender('女')" button>
                    <mu-list-item-action>
                        <mu-icon value="face" color="#e91e63"></mu-icon>
                    </mu-list-item-action>
                    <mu-list-item-title>女</mu-list-item-title>
                </mu-list-item>
            </mu-list>
        </mu-bottom-sheet>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {userMixins} from "../../../common/mixins"
    import FormatTime from '../../../common/utils/time'
    import {saveOrUpdateIInfo, queryIInfo} from "../../../api/personal"

    export default {
        name: "info",
        mixins: [userMixins],
        data() {
            return {
                /***
                 * 表单验证
                 */
                aliasRules: [
                    {validate: val => !!val, message: '必须填写用户昵称'},
                    {validate: val => val.length >= 2, message: '用户昵称长度大于或等于 2 '}
                ],
                ageRules: [
                    {validate: val => /^\d{1,2}$/.test(val), message: '请输入正确的年龄 '}
                ],
                workAgeRules: [
                    {validate: val => /^\d{1,2}$/.test(val), message: '请输入正确的从业年限 '}
                ],
                companyRules: [
                    {validate: val => val.length >= 2, message: '公司名称长度大于或等于 2 '}
                ],

                /***
                 * 表单内容：
                 *    alias: 昵称
                 */
                form: {
                    alias: '',
                    sex: '',
                    age: 0,
                    workAge: 0,
                    birthDate: null,
                    workTime: null,
                    company: '',
                },
                open: false,
                mode: 0,
                loading: false
            }
        },
        computed: {
            navigateTitle() {
                switch (this.mode) {
                    case 1:
                        return '修改个人资料'
                    default:
                        return '个人资料'
                }
            }
        },
        activated() {
            this.init()
        },
        methods: {
            init() {
                this.mode = 0

                this.form = {
                    alias: '',
                    sex: '',
                    age: 0,
                    workAge: 0,
                    birthDate: null,
                    workTime: null,
                    company: ''
                }

                this.open = false
                this.mode = 0
                this.loading = false

                queryIInfo().then(res => {
                    this.form = res

                    if (this.form.birthDate) {
                        this.form.age = this.getAgeByDate(this.form.birthDate)
                    }
                    if (this.form.workTime) {
                        this.form.workAge = this.getAgeByDate(this.form.workTime)
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
            submit() {
                this.$refs.form.validate().then(result => {
                    if (result) {
                        this.loading = true

                        this.form.birthDate = this.getDateByAge(this.form.age)
                        this.form.workTime = this.getDateByAge(this.form.workAge)

                        saveOrUpdateIInfo(this.form).then(res => {
                            this.loading = false

                            this.$toast('修改成功')

                            this.form.pwd = this.user.pwd

                            const info = Object.assign({}, this.user, this.form)

                            this.login(info)

                            this.$router.replace('/about/data')

                            this.$emit('update')
                        }).catch(err => {
                            this.loading = false
                        })
                    } else {
                        this.loading = false
                    }
                })
            },
            clear() {
                this.$refs.form.clear()

                this.form = {
                    alias: '',
                    sex: '',
                    age: '',
                    workAge: '',
                    birthDate: null,
                    workTime: null,
                    company: '',
                }
            },
            over() {
                this.mode = 0

                this.init()
            },
            handlerToggleMode() {
                this.mode = 1
            },
            // 切换性别
            handlerToggleGender(item) {
                this.form.sex = item
            },
            // 点击选择性别
            handlerSelected() {
                this.open = true
            },
            // 关闭
            closeBottomSheet() {
                this.open = false
            },
            getDateByAge(age) {
                return FormatTime.formatDate(new Date(new Date().getFullYear() - age, new Date().getMonth()), 'YYYY-MM')
            },
            getAgeByDate(date) {
                return new Date().getFullYear() - FormatTime.formatDate(date, "YYYY")
            }
        },
        components: {
            Navigate
        },
        created() {
            this.form.alias = this.$route.query.alias
        }
    }
</script>

<style lang="stylus" scoped>
    .about-info-editor
        padding-top 46px
        background-color: #fff
        .scroll-wrapper
            width 100%
            height 100%
            .form
                .form-item-s
                    margin 0
                    padding 15px

    >
    >
    >
    .mu-form-item-help
        bottom 5px

    .form-item-buttons
        display flex
        justify-content center
        padding 20px 0
        .button
            color #fff
            background-color #2196f3
            &.clear
                color #999
                background-color #fff

    .form-button-wrapper
        margin 20px
        .button
            width 100%
            background-color #2196f3
</style>



